import { AccountService } from './../../../Services/account/account.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { Login } from 'src/app/demo/_model/login';

@Component({
  selector: 'app-auth-signin-v2',
  templateUrl: './auth-signin-v2.component.html',
  styleUrls: ['./auth-signin-v2.component.scss']
})
export class AuthSigninV2Component implements OnInit {
  LoginForm: FormGroup;
  constructor(private formBuilder: FormBuilder, private AccountService: AccountService, private httpSv: HttpService, private toastr: ToastrService) { }

  ngOnInit() {
    this.initForm();
  }
  initForm() {
    this.LoginForm = this.formBuilder.group({
      mobileNumber: ['', Validators.required],
      password: ['', Validators.required],
    });
  }
  onFormSubmit() {
    let data: Login = this.LoginForm.value;
    this.AccountService.login(data).subscribe((res) => {
      this.toastr.success("تم تسجيل الدخول بنجاح")
      this.AccountService.getCurrentUser();
    },error => {
      this.toastr.error(error)
    }
    )
  }
}

